.truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
}
.photo_grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    justify-items: center;
}
.photo_card {
    padding: 1rem;
    background-color: #fff;
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid gray;
}
.photo_card .photo {
    width: 300px;
    height: 200px;
    object-fit: cover;
    border-radius: 8px;
    border:  1px solid gray;
    
}

@media (min-width: 1441px) {
    .photo_grid {
        grid-template-columns: repeat(4, 1fr); /* Exactly 4 columns */
    }
}

@media (max-width: 1440px) {
    .photo_grid {
        grid-template-columns: repeat(3, 1fr); /* Exactly 4 columns */
    }
}

@media (max-width: 1070px) {
    .photo_grid {
        grid-template-columns: repeat(2, 1fr); /* 2 columns on medium screens */
    }
}

@media (max-width: 700px) {
    .photo_grid {
        grid-template-columns: 1fr; /* 1 column on small screens */
    }
}

.clickable {
    cursor: pointer;
    transition: transform 0.2s ease;
}
.clickable:hover {
    transform: scale(1.02);
}
.modal_overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
.modal_overlay span{
    align-items: center;
}
.modal_photo {
    background: white;
    padding: 20px;
    border-radius: 10px;
    max-width: 80%;
    max-height: 90%;
    overflow-y: auto;
    position: relative;
    min-width:500px;
}

.modal_photo img {
    max-width: 100%;
    max-height: 70vh;
    display: block;
    margin: 0 auto 10px;
}

.close_modal{
    user-select: none;
}

.icon_close{
    position: absolute;
    top: 10px;
    right: 15px;
    cursor: pointer;
    background-color: #fff;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    padding:10px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    transition: background-color 0.2s ease, transform 0.2s ease;
    user-select: none;
    z-index: 999;
    opacity: 0.8;
}

.icon_close:hover {
    background-color: #f2f2f2;
    transform: scale(1.1);
    opacity: 1;
}

/* Checkmark button */
.photo_card .checkmark_button {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 30px;
    height: 30px;
    background-color: white;
    border-radius: 50%;
    border: 2px solid #333;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 5; /* Ensure the button is above the image */
    transition: background-color 0.3s ease;
    user-select: none;
    opacity: 85%;
  }
  
  .photo_card img{
    -webkit-user-drag: none;
  }

/* Default hover effect (scale image on hover) */
.photo_card img:hover {
    transform: scale(1.1); /* Default scaling */
    transition: transform 0.3s ease; /* Smooth transition */
    cursor:pointer;
  }
  
  /* Smaller screens: smaller scaling effect */
  @media (max-width: 700px) {
    .photo_card img:hover {
      transform: scale(1.05); /* Less scale on small screens */
    }
  }
  
  /* Medium screens: moderate scaling */
  @media (max-width: 1070px) {
    .photo_card img:hover {
      transform: scale(1.08); /* Slightly more scale on medium screens */
    }
  }
  
  /* Larger screens: stronger scaling effect */
  @media (min-width: 1071px) {
    .photo_card img:hover {
      transform: scale(1.12); /* More scale on larger screens */
    }
  }
  .photo_card .checkmark_button:hover {
    background-color: #f0f0f0;
  }
  
  /* The tick icon inside the button */
  .photo_card .checkmark_button .icon_tick {
    margin-top: 10px;
    object-fit: contain;
    user-select: none;
    
  }
  
  /* Ensure the photo card is positioned relatively for the absolute positioning of the button */
  .photo_card {
    position: relative;
    padding: 1rem;
    background-color: #fff;
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid gray;
    user-select: none;
  }
  
